html, body, .header, .img-link, .nav-link, .nav-list {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  --font-color: #00acdc;
}

body {
  text-transform: uppercase;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.img-link {
  text-decoration: none;
  display: flex;
}

.nav-list {
  list-style: none;
  display: flex;
}

.nav-link {
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav-link:hover {
  color: var(--font-color);
}

.is-active:after {
  content: "";
  background-color: var(--font-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: var(--font-color);
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}
